			type: Array,
			default: () => []
		}
	},
	methods: {
		// 跳转用户详情
		navUserInfo(data) {
			var that = this;
			if (data.myUser) {
				uni.navigateTo({
					url: '/pages/public/userInfo/userInfo?data=' + JSON.stringify(data)
